.input__wrapper {
  @apply flex flex-col z-0;
}
.input__label {
  @apply text-sm font-semibold mb-2;
}
.input {
  @apply relative flex items-center w-full; 
}
.input__field{
  @apply px-3 h-9 text-sm transition duration-300 border border-slate-300 rounded-md focus:outline-none focus:border-pari-blue-200 w-full ;
}
.input__field--search{
  @apply pl-10;
}
.input__field--password{
  @apply pr-10;
}
.input__field--error{
  @apply border-pari-red-500 bg-pari-red-50;
}
.input__wrapper .note{
  @apply mt-1 text-xs text-pari-gray-800;
}
.input__wrapper .validation{
  @apply mt-1 text-xs ;
}
.input__wrapper .validation--red{
  @apply text-pari-red-500;
}
.input__wrapper .validation--white{
  @apply text-white;
}
.input__icon {
  @apply absolute text-lg cursor-pointer;
}
.input__icon--search {
  @apply left-3 text-pari-gray-500;
}
.input__icon--password {
  @apply right-3 text-pari-gray-300;
}
.input__icon--password[aria-label="true"] {
  @apply text-pari-orange-500;
}
