.layout {
  @apply flex w-full overflow-x-hidden bg-slate-100;
}

/*SIDEBAR*/
.sidebar__wrapper,
.sidebar__logo,
.sidebar__logo img, 
.sidebar__menu,
.sidebar__menu span:nth-child(1), 
.sidebar__menu span:nth-child(2){
  @apply transition-all duration-200;
}
.sidebar__wrapper {
  @apply fixed left-0 bottom-0 top-0 z-40 -translate-x-[130%] flex lg:overflow-x-hidden  lg:-translate-x-0   lg:max-w-[12rem] ;
}
.sidebar__wrapper[aria-label="true"] {
  @apply -translate-x-0 lg:shadow-lg lg:max-w-[5rem] lg:hover:max-w-[12rem];
}
.sidebar {
  @apply w-52 bg-white shadow-lg h-screen flex-shrink-0 border-r z-40;
}
.sidebar__logo {
  @apply flex items-center h-16 px-5 mb-8 relative overflow-x-hidden;
}
.sidebar__logo img {
  @apply h-10 absolute top-3 left-5;
}
.sidebar__wrapper[aria-label="true"] .sidebar__logo img:nth-child(1) {
  @apply lg:opacity-0;
}
.sidebar__wrapper[aria-label="true"]:hover .sidebar__logo img:nth-child(1) {
  @apply lg:opacity-100;
}
.sidebar__menus {
  @apply text-sm flex flex-col w-full space-y-1;
}
.sidebar__menu {
  @apply px-5 py-2 lg:hover:bg-pari-blue-100 cursor-pointer w-full flex items-center;
}
.sidebar__menu[aria-label="active"] {
  @apply bg-pari-blue-50 lg:hover:bg-pari-blue-100 text-pari-blue-500;
}
.sidebar__menu span:nth-child(1){
  @apply max-w-[2.25rem] flex justify-center w-full;
}
.sidebar__menu span:nth-child(2){
  @apply font-semibold lg:max-w-full lg:overflow-hidden capitalize;
}

.sidebar__wrapper[aria-label="true"] .sidebar__menu span:nth-child(2){
  @apply lg:max-w-0;
}
.sidebar__wrapper[aria-label="true"]:hover .sidebar__menu span:nth-child(2){
  @apply lg:max-w-full;
}
.sidebar__close {
  @apply flex items-center justify-center w-8 h-8 mt-4 ml-4 bg-white rounded-full cursor-pointer lg:hidden absolute left-[13rem] top-0 z-50;
}

/*CONTENT*/
.content__wrapper {
  @apply flex flex-col w-full h-screen lg:ml-[12rem];
  transition: margin-left 0.2s;
}
.sidebar__wrapper[aria-label="true"] ~ .content__wrapper {
  @apply lg:ml-[5rem];
}
.content {
  @apply overflow-y-auto flex flex-col space-y-5 py-5 md:px-5 ;
}
.content__title__wrapper {
  @apply flex flex-col space-y-4 col-span-12 px-5 md:px-0 lg:flex-row lg:items-center lg:space-y-0;
}
.content__title {
  @apply text-lg font-semibold;
}
.content__actions {
  @apply flex items-center lg:ml-4 text-sm flex-wrap;
}

/*HEADER*/
.header {
  @apply flex items-center justify-between w-full h-16 px-2 text-white bg-pari-blue-500 flex-shrink-0;
}
.header__menus {
  @apply flex items-center;
}
.header__menu {
  @apply px-3 h-12 transition duration-200 flex items-center justify-center lg:hover:bg-pari-blue-550 rounded-md cursor-pointer;
}
.header__menu--icon {
  @apply text-2xl;
}
.header__menu--account {
  @apply flex items-center;
}
.header__menu--account .account__username {
  @apply hidden md:block text-sm font-medium mr-2;
}
.header__menu--account .account__image {
  @apply h-8 w-8 overflow-hidden rounded-full bg-pari-gray-300;
}
.header__notification {
  @apply relative;
}
.header__notification .notification__dot{
  @apply absolute top-0 right-0 w-2 h-2 rounded-full bg-pari-orange-500;
}


