.btn {
  @apply focus:outline-none border transition duration-300 rounded-md flex items-center justify-center font-medium;
}

/* SIZES */
.btn--sm {
  @apply px-2 h-8 text-xs space-x-1;
}
.btn--md {
  @apply px-3 h-9 text-sm space-x-1;
}
.btn--box-6 {
  @apply w-6 h-6 text-sm;
}
.btn--box-8 {
  @apply w-8 h-8 text-sm;
}

/* COLORS */
.btn--primary {
  @apply border-pari-blue-500 bg-pari-blue-500 text-white hover:bg-pari-blue-550 hover:border-pari-blue-550;
}
.btn--secondary {
  @apply border-pari-orange-500 bg-pari-orange-500 text-white lg:hover:bg-pari-orange-550 lg:hover:border-pari-orange-550;
}
.btn--disabled {
  @apply border-gray-300 bg-gray-300 text-gray-500 hover:border-gray-300 cursor-not-allowed;
}
.btn--gray {
  @apply bg-pari-gray-50 hover:bg-pari-gray-100;
}

/* TYPES */
.btn--primary[aria-label="border"] {
  @apply bg-transparent text-pari-blue-500 lg:hover:text-white lg:hover:bg-pari-blue-500;
}
.btn--secondary[aria-label="border"] {
  @apply bg-transparent text-pari-orange-500 lg:hover:text-white lg:hover:bg-pari-orange-500;
}
.btn--disabled[aria-label="border"] {
  @apply text-gray-500 border-gray-300 bg-gray-200;
}
.btn--gray[aria-label="pagination"] {
  @apply -space-x-2 text-pari-blue-500;
}
.btn--disabled[aria-label="pagination"] {
  @apply -space-x-2 text-gray-500 border-gray-300 bg-gray-200;
}
