.select__wrapper {
  @apply relative flex items-center text-sm;
}
.select {
  @apply flex items-center px-3 h-9 text-sm transition duration-300 border rounded-md focus:outline-none w-full truncate pr-10 cursor-pointer;
}
.select[aria-label="true"] {
  @apply border-pari-blue-200;
}
.select__options {
  @apply absolute z-10 hidden flex-col w-full bg-white border shadow-md overflow-y-auto max-h-40;
}
.select__options--top {
  @apply bottom-9;
}
.select__options--bottom {
  @apply top-9;
}
.select__options[aria-label='true'] {
  @apply flex;
}
.select__option {
  @apply px-3 py-2 flex lg:hover:bg-gray-100 transition duration-100 cursor-pointer;
}
.select__icon {
  @apply absolute right-3;
}
.select__icon[aria-label='true'] {
  @apply -rotate-180;
}