.accordion {
  @apply flex flex-col w-full;
}

.accordion > tr:nth-child(1) > td:last-child > span, 
.accordion > div:nth-child(1) > div:last-child > span 
{
  @apply transition duration-300 flex items-center;
}

.accordion[aria-label="true"] > tr:nth-child(1) > td:last-child > span, 
.accordion[aria-label="true"] > div:nth-child(1) > div:last-child > span 
{
  @apply -rotate-180;
}

.accordion > tr:nth-child(2),
.accordion > div:nth-child(2),
.accordion > span:nth-child(2)
{
  @apply max-h-0 overflow-y-hidden h-full transition-all duration-300;
}

